import React,{Component} from 'react';
//import logo from './logo.svg';
import './App.css';

import ButtonsPanel from './ButtonsPanel';
import InputStap from './InputStap';





class Counter extends Component {


	
	constructor(props){


		super(props);
		this.state = {
			initValue: this.props.initValue,
			numberValue: 1,
		}
	}

	changeNumber = () =>{ //функция изменяющая состояния элемента относительно его предыдущего состояния, 
		//console.log('Dodaj 1');

		let number = this.state.numberValue;
		console.log(number);
		this.setState((prevState)=>{
			//console.log(props.numberValue);
			return({initValue: prevState.initValue+number});
			//два параметра или обьект сnumberостояния или функция звортна
		});
	}

	reset = (reset) => {
		if(reset){
			
			this.setState({initValue: 0});
		}else{
			this.setState({initValue: this.props.initValue});//делали функцию, которая при тру приравнивает к 0, а при фолс берт число которое задано по дефолту 	
		}
	}
	
	
	
	
	
	
	

	
/*	startValue = () => {
		this.setState(stValue=>{
		return({initValue: this.props.initValue});	
		});
	}
	*/
	
/*	
	 callbackTest =()=>{
	
		 console.log('click w dziecko');
		 
	}*/
	 
	setNumberTo = (event) => {
		let valueNum = event.target.value;
		let valueNumb = Number(valueNum);
		console.log(valueNum);
		if (valueNum >= 1){
		this.setState({numberValue: valueNumb});
	}  else {
		this.setState({numberValue: 1})
	}
}	 
	 
	
	
	render(){
		
	  return (
		<div className="counter">
			To jest liczniki : {this.state.initValue};
			<ButtonsPanel 
		  changeCounterValue={this.changeNumber} 
		  resetCounterValue={this.reset} />
		<InputStap setNumber={this.setNumberTo}/>
		</div>
	  );
	}
}

export default Counter;
